import React from 'react';
import { Counter } from './features/counter/Counter';
import { Time } from './features/counter/Time';
import { MountChange  } from './features/counter/MountChange';
import { Losing } from './features/counter/Losing';
import { Players } from './features/counter/Players';
import { Finish } from './features/counter/Finish';
import { useSelector } from 'react-redux';
import { selectLosing, selectFinishGame } from './features/counter/counterSlice';
import './App.css';

function App() {
  const isLosing = useSelector(selectLosing);
  const isFinish = useSelector(selectFinishGame);
  
  return (
    <div className="App">
      <h2>Memory</h2>
      <div className="container">
        <div className="setting">
          <MountChange/>
          <Time/>
        </div>
        
        {isLosing ? <Losing/> : isFinish ? <Finish/> : <Counter />}
        {isFinish || isLosing? <div className="prop"></div> : <Players />}
      </div>
    </div>
  );
}

export default App;
