import { createSlice } from '@reduxjs/toolkit';

import bee from './img/bee.png';
import butterfly from './img/butterfly.png';
import elephant from './img/elephant.png';
import fox from './img/fox.png';
import lion from './img/lion.png';
import octopus from './img/octopus.png';
import penguin from './img/penguin.png';
import snake from './img/snake.png';
import owl from './img/owl.png';


const imgUrl = [bee, butterfly, elephant, fox, lion, octopus, penguin, snake, owl];
const arrImg = imgUrl.sort(()=> 0.5 - Math.random());  

 console.log(arrImg);


const initialState = {
  counter: 0, 
  size: 4, 
  startGame: false, 
  finishGame: false, 
  setTime: 60, 
  isLosing: false,
  players: [{id: 0, name: "Player 1", points: 0, victory: false}, {id: 1, name: "Player 2", points: 0, victory: false}],
  countPlayers: 0,
  movePlayer: "",
  cells: [],
  hidden: true,
  draw: false
};

export const counterSlice = createSlice({
  name: 'counter',
  initialState,
  reducers: {
    addCells: (state, action) => {
      state.size = Number(action.payload);
      state.cells = Array.from(Array(state.size), () => new Array(state.size));
      
      console.log( state.cells);
      let val = 0;
      for(let i = 0; i< state.cells.length; i++) {
        for (let j = 0; j< state.cells[i].length; j++) {
          if (val == 9 || val == state.size * state.size / 2) {
            val = 0;
          }
          state.cells[i][j] = {value: arrImg[val], visible: false};
          val++;
        }
      }
      for(let j = 0; j < 20; j++) {
        for(let i = 0; i< state.cells.length; i++) {
          state.cells[i].sort(() => Math.random() - 0.5);
          state.cells.sort(() => Math.random() - 0.5);
        }
      }

    },

    addElem: (state, action) => {
      
      let i = Math.trunc(action.payload / state.size);
      let j = action.payload - Math.trunc(action.payload / state.size) * state.size;

      state.cells[i][j].visible = true;
        if (state.counter % 2 == 0) {
          state.previusValue = {i: i, j: j};
        } else {
          if(state.cells[state.previusValue.i][state.previusValue.j].value != state.cells[i][j].value) {
            state.cells[state.previusValue.i][state.previusValue.j].visible = false;
            state.cells[i][j].visible = false;
            state.countPlayers++
          } else {
            if(state.countPlayers % 2 == 0) {
              state.players[0].points++;
            } else {
              state.players[1].points++;
            }
          }
        }

        if(state.countPlayers % 2 == 0) {
          state.movePlayer = state.players[0].name;
        } else {
          state.movePlayer = state.players[1].name;
        }

        state.counter++;
    },

    addTime: (state, action) => {
      if(state.counter > 0) state.startGame = true;
      state.time = action.payload;
    },

    setTime: (state, action) => {
     state.setTime = action.payload;
    },

    getFinish: (state) => {
      state.finishGame = state.cells.every(cell => {
        return cell.every(item => {
          return item.visible
        })
      })
      if(state.players[0].points == state.players[1].points) {
        state.draw = true;
      } else {
        state.draw = false;
      }
    },

    getLosing: (state, action) => {
      state.isLosing = action.payload
    },

    setPlayers: (state, action) => {
      state.players[action.payload.id].name = action.payload.value
    },

    setHidden: (state, action) => {
      state.hidden = action.payload;
    },
    
  },
});

export const { addCells, addElem, addTime, getFinish, setTime, getLosing, setPlayers, setSize, setHidden } = counterSlice.actions;
export const selectCells = (state) => state.counter.cells.flat();
export const selectSetTime = (state) => state.counter.setTime;
export const selectLosing = (state) => state.counter.isLosing;
export const selectStartGame = (state) => state.counter.startGame;
export const selectFinishGame = (state) => state.counter.finishGame;
export const selectPlayers = (state) => state.counter.players;
export const selectMovePlayer = (state) => state.counter.movePlayer;
export const selectSize = (state) => state.counter.size;
export const selectHidden = (state) => state.counter.hidden;
export const selectDraw = (state) => state.counter.draw;

export default counterSlice.reducer;

 
