import React from 'react';
import { setPlayers, selectPlayers, selectStartGame, selectMovePlayer }  from './counterSlice';
import { useSelector, useDispatch } from 'react-redux';



export function Players() {
const dispatch = useDispatch();    
const players = useSelector(selectPlayers);  
const start = useSelector(selectStartGame);  
const player = useSelector(selectMovePlayer);
 
return (
        <div className="players">
            {!start ?
                players.map((item, idx) => 
                <div className={`player-${item.id + 1}`}  key={item.id}>
                    <label htmlFor={idx}>Введите имя ИГРОК №{item.id + 1}: </label>
                    <input id={idx} 
                        onChange={(e) => dispatch(setPlayers({id: e.target.id, value: e.target.value}))} 
                        type="text" 
                        value={players[idx].name} 
                        placeholder={`Player ${item.id + 1}`}/>
                </div>) :
                <div className="outPlayers">
                    <h1 className="toPlayer">{player}</h1>
                    <div className="outPlayers-item">
                        {players.map((item, idx) => 
                            <div className={`player-${item.id + 1}`} key={item.id}>
                                <p>{players[idx].name}:</p>
                                <p>Очков: <span className="points">{players[idx].points}</span></p>
                            </div>)} 
                    </div>   
                </div>      
            }
        </div>
    
    );
}
