import React, {useEffect} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {  addElem,  selectCells, getFinish, selectSize, selectHidden } from './counterSlice';

export function Counter() {
  const dispatch = useDispatch();
  const cells = useSelector(selectCells);
  const size = useSelector(selectSize);
  const hidden = useSelector(selectHidden);
  console.log(hidden);
  // const arrCells = cells.flat();
  // const oneDimArr = cells.reduce((a, b) => [...a, ...b], []);
  // console.log(cells);
      
  const handleClick = (e) => {
    dispatch(addElem(e.target.id));
    dispatch(getFinish());
  }

  return (
    <>
      <div className="memory" style={{display: hidden ? "none" : "flex"}}>
        {cells.map((item, idx)=> 
          <button 
            style={{width: `${100/size}%`, height: `${100/size*5}px`}} 
            className="cell" 
            id={idx} 
            key={idx} 
            onClick={handleClick} 
            disabled={item.visible}>
            {!item.visible ? "" : <img  src={item.value} alt="" key={`img-${item.idx}`} width="80%"></img>}
          </button>)} 
      </div>
   
    </>
   );
}