import React, { useState, useEffect} from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { addTime, setTime, getLosing, selectSetTime, selectStartGame, selectFinishGame} from './counterSlice';

export function Time() {
  const dispatch = useDispatch();
  const setTimeAction = useSelector(selectSetTime);
  const start = useSelector(selectStartGame);
  const finish = useSelector(selectFinishGame);
  let [count, setCount] = useState(0);
  
  useEffect(() => {
    let interval = setInterval(() => {
    dispatch(addTime(count));      
    start ? setCount(count + 1) : setCount(0) ;
    }, 1000);

    if (finish) clearInterval(interval); 
    if (count == setTimeAction && setTimeAction != ""){
        dispatch(getLosing(true)); 
        clearInterval(interval);  
    } 

    return () => clearInterval(interval); 
  });
   
  return (
    <div className="time"  >
        {start ? 
            <>
                <div className="getTime"><span>Время игры:</span>{count}</div>   
                <div className="setTime">
                    <div className="setTime-item"><p className="setTime-item-p"><span>Лимит времени:</span>{setTimeAction}</p></div> 
                </div>  
            </> : 
            <div className="setTime-item">
                <label htmlFor="settime">Установите время: </label>
                <input id="settime" onChange={(e) => dispatch(setTime(e.target.value))} type="number" min="5" max="600" value={setTimeAction}/>
            </div>
        }
    </div>
    );
}